.viewRoomContainer {
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
    box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.3);
  
    h1 {
      color: gray;
    }
    .editable{
        position: absolute;
        top: 100px;
        right: 50px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 50px;
        width: 50px;
        border: 1px solid;
        cursor: pointer;
    }
    .addRoomWrapper {
      display: flex;
      gap: 20px;
      margin-top: 20px;
  
      .left {
        width: 50%;
        display: flex;
        flex-direction: column;
        .top {
          padding: 10px 20px;
        }
        .bottom {
          padding: 10px;
  
          label {
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border: 1px solid black;
            border-radius: 20px;
            width: 150px;
            height: 150px;
            cursor: pointer;
            font-weight: bold;
  
            span {
              font-weight: 300;
              font-size: small;
            }
            input {
              display: none;
            }
          }
          .imageContainer {
            display: flex;
            margin-top: 20px;
            padding: 10px;
            gap: 10px;
            flex-wrap: wrap;
            justify-content: center;
  
            .image {
              width: 30%;
              position: relative;
            }
            img {
              width: 100%;
              aspect-ratio: 1;
              overflow: hidden;
              object-fit: cover;
              background-attachment: fixed;
              border-radius: 15px;
            }
            .deleteButton {
              position: absolute;
              top: 5px;
              right: 5px;
              width: 30px;
              height: 30px;
              padding: 0;
              border-radius: 50%;
              background-color: red;
              color: lightgray;
              border-color: red;
              overflow: hidden;
              cursor: pointer;
            }
          }
        }
      }
      .right {
        width: 50%;
        display: flex;
        flex-direction: column;
        .top {
          padding: 10px 20px;
        }
        .bottom {
          display: flex;
          padding: 15px;
          flex-wrap: wrap;
          gap: 7%;
  
          .item {
            display: flex;
            flex-direction: column;
            margin: 0;
            padding: 0;
            width: 45%;
            position: relative;
            margin-top: 5px;
  
            label {
              font-size: 90%;
              font-weight: 300;
              margin-top: 10px;
            }
  
            input[type="text"],
            input[type="number"],
            .allowance {
              background-color: transparent;
              height: 24px;
              width: 100%;
              border: 2px solid black;
              border-radius: 5px;
              padding-left: 10px;
              padding: 5px 12px;
            }
            input[type="radio"] {
              margin-left: 20px;
              margin-right: 2px;
            }
            .options {
              position: absolute;
              top: 65px;
              background-color: white;
              color: gray;
              border-radius: 5px;
              box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.3);
              z-index: 1;
  
              .optionItem {
                width: 150px;
                display: flex;
                justify-content: space-between;
                margin: 10px;
              }
              .optionCounter {
                display: flex;
                align-items: center;
                gap: 10px;
                font-size: 12px;
                color: black;
  
                .CounterButton {
                  width: 25px;
                  height: 25px;
                  border: 1px solid black;
                  cursor: pointer;
                  background-color: white;
  
                  &:disabled {
                    cursor: not-allowed;
                  }
                }
              }
            }
            .saveButton {
              width: 35%;
              margin-top: 60px;
              align-self: center;
              height: 35px;
              border-radius: 5px;
              background: linear-gradient(to right, #ff512f, #f09819);
              cursor: pointer;
              font-size: 1em;
              font-weight: bold;
              border: 1px solid;
              color: white;
              
              &:hover{
                background: linear-gradient(
                  to left,
                  #ff512f,
                  #f09819
                );
            }
          }
        }
      }
    }
  }
  }