.container{
	margin: 0;
	padding: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	font-family: 'Jost', sans-serif; 
	background: linear-gradient(
                        to bottom,
                        #ff512f,
                        #f09819
                    );
	

    .main{
        width: 350px;
        height: 500px;
        overflow: hidden;
        border-radius: 10px;
        box-shadow: 5px 20px 50px #000;
		

        #chk{
            display: none;
			&:checked {
				& ~ .login {
					transform: translateY(-500px);
					label {
						transform: scale(1);
					}
				}
				& ~ .signup {
					label {
						transform: scale(.6);
					}
				}
			}
        }
        .signup{
            position: relative;
            width:100%;
            height: 100%;
        }
        .login{
            height: 460px;
            background: #eee;
            border-radius: 60% / 10%;
            transform: translateY(-180px);
            transition: .8s ease-in-out;

            label{
                color: #ff512f;
                transform: scale(.6);
            }
            .ForgotPassword{
                text-align: center;
                margin-top: 20px;
        
                a{
                    text-decoration: none;
                    cursor: pointer;
                }
            }
        }
		
label{
	color: #fff;
	font-size: 2.3em;
	justify-content: center;
	display: flex;
	margin: 60px;
	font-weight: bold;
	cursor: pointer;
	transition: .5s ease-in-out;
}
input{
	width: 60%;
	height: 20px;
	background: #e0dede;
	justify-content: center;
	display: flex;
	margin: 20px auto;
	padding: 10px;
	border: none;
	outline: none;
	border-radius: 5px;
}
button{
	width: 60%;
	height: 40px;
	margin: 10px auto;
	justify-content: center;
	display: block;
	color: #fff;
	background: linear-gradient(
		to right,
		#ff512f,
		#f09819
	);
	font-size: 1em;
	font-weight: bold;
	margin-top: 20px;
	outline: none;
	border: none;
	border-radius: 5px;
	transition: .2s ease-in;
	cursor: pointer;
	border: 1px solid ;

	&:hover{
		background: linear-gradient(
			to left,
			#ff512f,
			#f09819
		);
	}
}

a{
	color: #ff512f;
}
    }
}

